'use client';

import { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import { Logger } from '../../../../shared/logger';
import { calculateWriterStats, type WriterStats as WriterStatsType } from '../editor/WriterStats';
import { useAutoSave } from './useAutoSave';
import { ProjectCharacter, ProjectStructure, ProjectNote } from '../../../../shared/types';

// Ï†ÄÏû• ÏÉÅÌÉú ÌÉÄÏûÖ
type SaveStatus = 'unsaved' | 'saving' | 'saved' | 'error';

interface UseProjectDataReturn {
  // üî• Î°úÎî© Î∞è ÏóêÎü¨ ÏÉÅÌÉú
  isLoading: boolean;
  error: string | null;
  
  // ÌîÑÎ°úÏ†ùÌä∏ ÏÉÅÌÉú
  title: string;
  setTitle: (title: string) => void;
  content: string;
  setContent: (content: string) => void;
  lastSaved: Date | null;
  saveStatus: SaveStatus;
  
  // ÏûëÍ∞Ä Îç∞Ïù¥ÌÑ∞
  characters: ProjectCharacter[];
  setCharacters: (characters: ProjectCharacter[]) => void;
  structure: ProjectStructure[];
  setStructure: (structure: ProjectStructure[]) => void;
  notes: ProjectNote[]; // üî• notes Ï∂îÍ∞Ä
  setNotes: (notes: ProjectNote[]) => void; // üî• setNotes Ï∂îÍ∞Ä
  writerStats: WriterStatsType;
  
  // Ïï°ÏÖò
  loadProject: () => Promise<void>;
  saveProject: () => Promise<void>;
  forceSave: () => Promise<void>; // üî• Ï¶âÏãú Ï†ÄÏû• (Ctrl+S)
  updateWriterStats: () => void;
  setWordGoal: (goal: number) => void;
}

export function useProjectData(projectId: string): UseProjectDataReturn {
  const sessionStartRef = useRef<number>(Date.now());
  
  // üî• ÏÑ±Îä• ÏµúÏ†ÅÌôî: Í∏∞Î≥∏ ÎÇ†Ïßú Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖò (new Date() Î∞òÎ≥µ ÏÉùÏÑ± Î∞©ÏßÄ)
  const defaultDate = useMemo(() => new Date(), []);
  
  // üî• ÏÑ±Îä• ÏµúÏ†ÅÌôî: Í∏∞Î≥∏ Îç∞Ïù¥ÌÑ∞ Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖò (Ï§ëÎ≥µ Í∞ùÏ≤¥ ÏÉùÏÑ± Î∞©ÏßÄ)
  const defaultCharacters = useMemo(() => [
    { 
      id: '1', 
      projectId: projectId,
      name: 'Ï£ºÏù∏Í≥µ', 
      role: 'Ï£ºÏöî Ïù∏Î¨º', 
      notes: 'Ïö©Í∞êÌïòÍ≥† Ï†ïÏùòÎ°úÏö¥ ÏÑ±Í≤©',
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
    { 
      id: '2', 
      projectId: projectId,
      name: 'Ï°∞Î†•Ïûê', 
      role: 'Ï°∞Î†•Ïûê', 
      notes: 'ÏßÄÌòúÎ°≠Í≥† Í≤ΩÌóòÏù¥ ÎßéÏùå',
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
    { 
      id: '3', 
      projectId: projectId,
      name: 'Ï†ÅÎåÄÏûê', 
      role: 'Ï†ÅÎåÄÏûê', 
      notes: 'ÏïºÎßùÏù¥ ÌÅ¨Í≥† ÎÉâÌòπÌï®',
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
  ], [projectId, defaultDate]);

  const defaultNotes = useMemo(() => [
    { 
      id: '1', 
      projectId: projectId,
      title: 'Ï≤´ Î≤àÏß∏ Î©îÎ™®',
      content: 'Ïù¥ÏïºÍ∏∞Ïùò ÌïµÏã¨ ÏïÑÏù¥ÎîîÏñ¥Î•º Ïó¨Í∏∞Ïóê Ï†ÅÏñ¥Î≥¥ÏÑ∏Ïöî.',
      tags: ['ÏïÑÏù¥ÎîîÏñ¥'],
      color: '#3b82f6',
      isPinned: false,
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
    { 
      id: '2', 
      projectId: projectId,
      title: 'ÏÑ§Ï†ï ÎÖ∏Ìä∏',
      content: 'ÏÑ∏Í≥ÑÍ¥Ä, Î∞∞Í≤Ω ÏÑ§Ï†ïÏóê ÎåÄÌïú ÎÇ¥Ïö©ÏùÑ Ï†ïÎ¶¨Ìï©ÎãàÎã§.',
      tags: ['ÏÑ§Ï†ï', 'ÏÑ∏Í≥ÑÍ¥Ä'],
      color: '#10b981',
      isPinned: true,
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
  ], [projectId, defaultDate]);

  const defaultStructure = useMemo(() => [
    { 
      id: '1', 
      projectId: projectId,
      type: 'chapter' as const, 
      title: '1Ïû•: ÏãúÏûë',
      isActive: true,
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
    { 
      id: '2', 
      projectId: projectId,
      type: 'scene' as const, 
      title: 'Ï≤´ Î≤àÏß∏ Ïû•Î©¥',
      isActive: true,
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
    { 
      id: '3', 
      projectId: projectId,
      type: 'scene' as const, 
      title: 'Îëê Î≤àÏß∏ Ïû•Î©¥',
      isActive: true,
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
    { 
      id: '4', 
      projectId: projectId,
      type: 'chapter' as const, 
      title: '2Ïû•: Ï†ÑÍ∞ú',
      isActive: true,
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
    { 
      id: '5', 
      projectId: projectId,
      type: 'scene' as const, 
      title: 'ÏÑ∏ Î≤àÏß∏ Ïû•Î©¥',
      isActive: true,
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
    { 
      id: '6', 
      projectId: projectId,
      type: 'note' as const, 
      title: 'ÏïÑÏù¥ÎîîÏñ¥ Î©îÎ™®',
      isActive: true,
      createdAt: defaultDate,
      updatedAt: defaultDate
    },
  ], [projectId, defaultDate]);
  
  // üî• Î°úÎî© Î∞è ÏóêÎü¨ ÏÉÅÌÉú Ï∂îÍ∞Ä
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  
  // üî• Í∏∞Î≥∏ ÌîÑÎ°úÏ†ùÌä∏ ÏÉÅÌÉú
  const [title, setTitle] = useState<string>('');
  const [content, setContent] = useState<string>('');
  const [lastSaved, setLastSaved] = useState<Date | null>(null);
  const [saveStatus, setSaveStatus] = useState<SaveStatus>('saved');
  
  // üî• refÎ°ú ÏµúÏã† Í∞í Ï∂îÏ†Å (ÏÑ±Îä• ÏµúÏ†ÅÌôî: useEffect Ï†úÍ±∞)
  const titleRef = useRef<string>('');
  const contentRef = useRef<string>('');
  
  // üî• ÏµúÏ†ÅÌôî: setterÏóêÏÑú ÏßÅÏ†ë ref ÏóÖÎç∞Ïù¥Ìä∏ (useEffect Î∂àÌïÑÏöî)
  const setTitleOptimized = useCallback((newTitle: string) => {
    titleRef.current = newTitle;
    setTitle(newTitle);
  }, []);
  
  const setContentOptimized = useCallback((newContent: string) => {
    contentRef.current = newContent;
    setContent(newContent);
  }, []);
  
  // üî• ÏûëÍ∞Ä Îç∞Ïù¥ÌÑ∞
  const [characters, setCharacters] = useState<ProjectCharacter[]>([]);
  const [structure, setStructure] = useState<ProjectStructure[]>([]);
  const [notes, setNotes] = useState<ProjectNote[]>([]); // üî• notes ÏÉÅÌÉú Ï∂îÍ∞Ä
  const [writerStats, setWriterStats] = useState<WriterStatsType>({
    wordCount: 0,
    charCount: 0,
    paragraphCount: 0,
    readingTime: 0,
    wordGoal: 1000,
    progress: 0,
    sessionTime: 0,
    wpm: 0
  });

  // üî• ÌîÑÎ°úÏ†ùÌä∏ Î°úÎìú (Î¨¥ÌïúÎ£®ÌîÑ Î∞©ÏßÄ)
  const loadProject = useCallback(async (): Promise<void> => {
    try {
      setIsLoading(true);
      setError(null);
      Logger.debug('PROJECT_DATA', 'Loading project', { projectId });
      
      const result = await window.electronAPI.projects.getById(projectId);
      if (result.success && result.data) {
        setTitle(result.data.title);
        setContent(result.data.content);
        setLastSaved(new Date(result.data.lastModified));
        setSaveStatus('saved'); // üî• Ï†ÄÏû• ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
        
        // üî• Ïã§Ï†ú Îç∞Ïù¥ÌÑ∞ Î°úÎìú - Ï∫êÎ¶≠ÌÑ∞ Îç∞Ïù¥ÌÑ∞
        try {
          const charactersResult = await window.electronAPI.projects.getCharacters(projectId);
          if (charactersResult.success && charactersResult.data) {
            setCharacters(charactersResult.data);
            Logger.debug('PROJECT_DATA', 'Characters loaded successfully', { count: charactersResult.data.length });
          } else {
            Logger.warn('PROJECT_DATA', 'No characters found, using defaults');
            // Í∏∞Î≥∏ Ï∫êÎ¶≠ÌÑ∞ Îç∞Ïù¥ÌÑ∞
            setCharacters(defaultCharacters);
          }
        } catch (error) {
          Logger.warn('PROJECT_DATA', 'Failed to load characters, using defaults', error);
          setCharacters(defaultCharacters);
        }
        
        // üî• Ïã§Ï†ú Îç∞Ïù¥ÌÑ∞ Î°úÎìú - Íµ¨Ï°∞ Îç∞Ïù¥ÌÑ∞
        try {
          const structureResult = await window.electronAPI.projects.getStructure(projectId);
          if (structureResult.success && structureResult.data) {
            setStructure(structureResult.data);
            Logger.debug('PROJECT_DATA', 'Structure loaded successfully', { count: structureResult.data.length });
          } else {
            Logger.warn('PROJECT_DATA', 'No structure found, using defaults');
            // Í∏∞Î≥∏ Íµ¨Ï°∞ Îç∞Ïù¥ÌÑ∞
            setStructure(defaultStructure);
          }
        } catch (error) {
          Logger.warn('PROJECT_DATA', 'Failed to load structure, using defaults', error);
          setStructure(defaultStructure);
        }
        
        // üî• Ïã§Ï†ú Îç∞Ïù¥ÌÑ∞ Î°úÎìú - ÎÖ∏Ìä∏ Îç∞Ïù¥ÌÑ∞
        try {
          const notesResult = await window.electronAPI.projects.getNotes(projectId);
          if (notesResult.success && notesResult.data) {
            setNotes(notesResult.data);
            Logger.debug('PROJECT_DATA', 'Notes loaded successfully', { count: notesResult.data.length });
          } else {
            Logger.warn('PROJECT_DATA', 'No notes found, using defaults');
            // Í∏∞Î≥∏ ÎÖ∏Ìä∏ Îç∞Ïù¥ÌÑ∞
            setNotes(defaultNotes);
          }
        } catch (error) {
          Logger.warn('PROJECT_DATA', 'Failed to load notes, using defaults', error);
          setNotes(defaultNotes);
        }
        
        Logger.info('PROJECT_DATA', 'Project loaded successfully');
      } else {
        throw new Error(result.error || 'Failed to load project');
      }
    } catch (error) {
      Logger.error('PROJECT_DATA', 'Error loading project', error);
      setError(error instanceof Error ? error.message : 'Failed to load project');
      
      // üî• Ïã§Ìå® Ïãú Î°úÏª¨ Î∞±ÏóÖ ÌôïÏù∏
      try {
        const backup = localStorage.getItem(`project_backup_${projectId}`);
        if (backup) {
          const backupData = JSON.parse(backup);
          setTitle(backupData.title || '');
          setContent(backupData.content || '');
          setSaveStatus('unsaved');
          Logger.info('PROJECT_DATA', 'Loaded from local backup');
        }
      } catch (storageError) {
        Logger.error('PROJECT_DATA', 'Failed to load backup', storageError);
      }
    } finally {
      setIsLoading(false); // üî• Î¨¥Ï°∞Í±¥ Î°úÎî© ÏÉÅÌÉú Ìï¥Ï†ú
    }
  }, [projectId]);

  // üî• ÌîÑÎ°úÏ†ùÌä∏ Ï†ÄÏû• Ìï®Ïàò (refÎ°ú Î¨¥ÌïúÎ£®ÌîÑ Î∞©ÏßÄ)
  const saveProjectInternal = useCallback(async (): Promise<void> => {
    try {
      const currentTitle = titleRef.current;
      const currentContent = contentRef.current;
      
      if (!currentTitle.trim() && !currentContent.trim()) return;
      
      Logger.debug('PROJECT_DATA', 'Saving project to server', { projectId });
      
      // üî• Î°úÏª¨ Î∞±ÏóÖ Î®ºÏ†Ä Ï†ÄÏû• (Ï¶âÏãú)
      try {
        const backupData = { title: currentTitle, content: currentContent, lastModified: new Date() };
        localStorage.setItem(`project_backup_${projectId}`, JSON.stringify(backupData));
        Logger.debug('PROJECT_DATA', 'Local backup saved');
      } catch (storageError) {
        Logger.warn('PROJECT_DATA', 'Failed to save local backup', storageError);
      }
      
      // üî• Ï¶âÏãú ÏÑúÎ≤Ñ Ï†ÄÏû•
      const result = await window.electronAPI.projects.update(projectId, {
        title: currentTitle,
        content: currentContent,
        lastModified: new Date()
      });
      
      if (result.success) {
        setLastSaved(new Date());
        setSaveStatus('saved');
        Logger.info('PROJECT_DATA', 'Project saved successfully to server');
        
        // ÏÑ±Í≥µ Ïãú Î°úÏª¨ Î∞±ÏóÖ Ï†úÍ±∞
        try {
          localStorage.removeItem(`project_backup_${projectId}`);
        } catch (e) {
          // Î¨¥Ïãú
        }
      } else {
        throw new Error(result.error || 'Failed to save project');
      }
    } catch (error) {
      Logger.error('PROJECT_DATA', 'Error saving project', error);
      setSaveStatus('error');
      throw error;
    }
  }, [projectId]); // üî• projectIdÎßå dependencyÎ°ú ÏÑ§Ï†ï

  // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº autoSave Hook ÏÇ¨Ïö© - ÌÉÄÏù¥Ìïë Ï§ëÎã® ÌõÑ Ï†ÄÏû•
  const { debouncedSave, forceSave, isLoading: isSaving } = useAutoSave({
    projectId,
    delay: 3000, // üî• 3Ï¥à ÎîúÎ†àÏù¥ (Îçî ÏïàÏ†ÑÌïú ÌÉÄÏù¥Ìïë) - ÌÉÄÏù¥Ìïë Î©àÏ∂ò ÌõÑÏóêÎßå Ï†ÄÏû•
    onSave: saveProjectInternal,
    onSaveSuccess: () => {
      setSaveStatus('saved');
      setLastSaved(new Date());
      Logger.info('PROJECT_DATA', '‚úÖ ÏûêÎèô Ï†ÄÏû• ÏôÑÎ£å', { projectId });
    },
    onSaveError: (error: Error) => {
      setSaveStatus('error');
      Logger.error('PROJECT_DATA', '‚ùå ÏûêÎèô Ï†ÄÏû• Ïã§Ìå®', { error: error.message, projectId });
      
      // ÏóêÎü¨ Î∞úÏÉù Ïãú Î°úÏª¨ Î∞±ÏóÖ ÏÉùÏÑ±
      try {
        localStorage.setItem(`project_backup_${projectId}`, JSON.stringify({ title, content }));
        Logger.info('PROJECT_DATA', 'üì¶ Î°úÏª¨ Î∞±ÏóÖ Ï†ÄÏû•Îê®', { projectId });
      } catch (backupError) {
        Logger.error('PROJECT_DATA', '‚ùå Î°úÏª¨ Î∞±ÏóÖ Ïã§Ìå®', backupError);
      }
    }
  });

  // üî• Ìò∏ÌôòÏÑ±ÏùÑ ÏúÑÌïú saveProject Ìï®Ïàò
  const saveProject = useCallback(async (): Promise<void> => {
    await forceSave();
  }, [forceSave]);
  
  // üî• ÎπÑÏö©Ïù¥ ÌÅ∞ ÌÜµÍ≥Ñ Í≥ÑÏÇ∞ÏùÑ Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖò (Hook Í∑úÏπô Ï§ÄÏàò)
  const memoizedStats = useMemo(() => {
    if (!content) return writerStats;
    return calculateWriterStats(content, writerStats.wordGoal, sessionStartRef.current);
  }, [content, writerStats.wordGoal, writerStats]);

  // üî• ÏûëÍ∞Ä ÌÜµÍ≥Ñ ÏóÖÎç∞Ïù¥Ìä∏ (Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖòÎêú Í∞í ÏÇ¨Ïö©)
  const updateWriterStats = useCallback((): void => {
    setWriterStats(memoizedStats);
  }, [memoizedStats]);
  
  const setWordGoal = useCallback((goal: number): void => {
    setWriterStats((prev: WriterStatsType) => ({
      ...prev,
      wordGoal: goal,
      progress: Math.min(100, Math.round((prev.wordCount / goal) * 100))
    }));
  }, []);

  // üî• ÌîÑÎ°úÏ†ùÌä∏ Ï¥àÍ∏∞ Î°úÎìú (ÏÑ±Îä• ÏµúÏ†ÅÌôî: loadProjectÎ•º useRefÎ°ú ÏïàÏ†ÑÌïòÍ≤å Í¥ÄÎ¶¨)
  const loadProjectRef = useRef(loadProject);
  loadProjectRef.current = loadProject;
  
  useEffect(() => {
    if (projectId) {
      loadProjectRef.current();
    }
  }, [projectId]); // üî• projectIdÎßå dependencyÎ°ú - Î¨¥ÌïúÎ£®ÌîÑ ÏôÑÏ†Ñ Î∞©ÏßÄ

  // üî• ÏûêÎèô Ï†ÄÏû• ÏãúÏä§ÌÖú (ÏÑ±Îä• ÏµúÏ†ÅÌôî: refÎ°ú Î¨¥ÌïúÎ£®ÌîÑ Î∞©ÏßÄ)
  const debouncedSaveRef = useRef(debouncedSave);
  debouncedSaveRef.current = debouncedSave;
  
  useEffect(() => {
    if (title.trim() || content.trim()) {
      setSaveStatus('unsaved');
      debouncedSaveRef.current(); // refÎ•º ÌÜµÌï¥ ÏïàÏ†ÑÌïòÍ≤å Ìò∏Ï∂ú
    }
  }, [title, content]); // üî• debouncedSave dependency ÏôÑÏ†Ñ Ï†úÍ±∞

  // üî• Ï†ÄÏû• Ï§ë ÏÉÅÌÉú Í¥ÄÎ¶¨
  useEffect(() => {
    if (isSaving) {
      setSaveStatus('saving');
    }
  }, [isSaving]);

  // üî• ÌÜµÍ≥Ñ ÏóÖÎç∞Ïù¥Ìä∏ (Í∏∞Í∞ÄÏ∞®Îìú ÏàòÏ†ï: interval Ï†úÍ±∞Î°ú Ïª§ÏÑú Î¶¨ÏÖã ÏôÑÏ†Ñ Ìï¥Í≤∞)
  useEffect(() => {
    updateWriterStats();
    // üî• 30Ï¥à interval ÏôÑÏ†Ñ Ï†úÍ±∞ - Ïª§ÏÑú Î¶¨ÏÖã ÏõêÏù∏ Ï†úÍ±∞
    // ÏÑ∏ÏÖò ÏãúÍ∞ÑÏùÄ ÏÇ¨Ïö©ÏûêÍ∞Ä ÌÜµÍ≥ÑÎ•º Î≥º ÎïåÎßå Í≥ÑÏÇ∞ÌïòÎèÑÎ°ù Î≥ÄÍ≤Ω
  }, []); // üî• dependency ÏôÑÏ†Ñ Ï†úÍ±∞ - useEffect ÏßÄÏò• Ìï¥Í≤∞

  // üî• Ï∫êÎ¶≠ÌÑ∞ Ï†ÄÏû• Ìï®Ïàò
  const saveCharacters = useCallback(async (charactersToSave: ProjectCharacter[]): Promise<void> => {
    try {
      Logger.debug('PROJECT_DATA', 'Saving characters', { count: charactersToSave.length });
      
      // üî• Ïã§Ï†ú API Ìò∏Ï∂ú
      const result = await window.electronAPI.projects.updateCharacters(projectId, charactersToSave);
      
      if (result.success) {
        Logger.info('PROJECT_DATA', 'Characters saved successfully');
      } else {
        throw new Error(result.error || 'Failed to save characters');
      }
    } catch (error) {
      Logger.error('PROJECT_DATA', 'Error saving characters', error);
      throw error;
    }
  }, [projectId]);

  // üî• Î©îÎ™® Ï†ÄÏû• Ìï®Ïàò
  const saveNotes = useCallback(async (notesToSave: ProjectNote[]): Promise<void> => {
    try {
      Logger.debug('PROJECT_DATA', 'Saving notes', { count: notesToSave.length });
      
      // üî• Ïã§Ï†ú API Ìò∏Ï∂ú
      const result = await window.electronAPI.projects.updateNotes(projectId, notesToSave);
      
      if (result.success) {
        Logger.info('PROJECT_DATA', 'Notes saved successfully');
      } else {
        throw new Error(result.error || 'Failed to save notes');
      }
    } catch (error) {
      Logger.error('PROJECT_DATA', 'Error saving notes', error);
      throw error;
    }
  }, [projectId]);

  // üî• Ï∫êÎ¶≠ÌÑ∞ Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨ (ÏûêÎèô Ï†ÄÏû• Ìè¨Ìï®)
  const handleCharactersChange = useCallback(async (newCharacters: ProjectCharacter[]): Promise<void> => {
    setCharacters(newCharacters);
    
    try {
      await saveCharacters(newCharacters);
    } catch (error) {
      Logger.error('PROJECT_DATA', 'Failed to save characters automatically', error);
      // ÏÇ¨Ïö©ÏûêÏóêÍ≤å ÏóêÎü¨ ÌëúÏãúÌï† Ïàò ÏûàÏùå
    }
  }, [saveCharacters]);

  // üî• Î©îÎ™® Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨ (ÏûêÎèô Ï†ÄÏû• Ìè¨Ìï®)  
  const handleNotesChange = useCallback(async (newNotes: ProjectNote[]): Promise<void> => {
    setNotes(newNotes);
    
    try {
      await saveNotes(newNotes);
    } catch (error) {
      Logger.error('PROJECT_DATA', 'Failed to save notes automatically', error);
      // ÏÇ¨Ïö©ÏûêÏóêÍ≤å ÏóêÎü¨ ÌëúÏãúÌï† Ïàò ÏûàÏùå
    }
  }, [saveNotes]);

  return {
    // üî• Î°úÎî© Î∞è ÏóêÎü¨ ÏÉÅÌÉú
    isLoading,
    error,
    
    // üî• Í∏∞Î≥∏ ÌîÑÎ°úÏ†ùÌä∏ Îç∞Ïù¥ÌÑ∞ (ÏÑ±Îä• ÏµúÏ†ÅÌôîÎêú setter)
    title,
    setTitle: setTitleOptimized,
    content,
    setContent: setContentOptimized,
    lastSaved,
    saveStatus,
    
    // üî• ÏûëÍ∞Ä Îç∞Ïù¥ÌÑ∞
    characters,
    setCharacters,
    structure,
    setStructure,
    notes, // üî• notes Ï∂îÍ∞Ä
    setNotes, // üî• setNotes Ï∂îÍ∞Ä
    writerStats,
    
    // üî• Ïï°ÏÖò Ìï®ÏàòÎì§
    loadProject,
    saveProject,
    forceSave, // üî• ÏÉàÎ°úÏö¥ Ï¶âÏãú Ï†ÄÏû• Ìï®Ïàò
    updateWriterStats,
    setWordGoal
  };
}
