// EasyMDE ÌÉÄÏûÖ Ï†ïÏùò
interface EasyMDEEditor {
  codemirror: CodeMirrorEditor;
  value(): string;
  value(val: string): void;
  togglePreview(): void;
  toggleSideBySide(): void;
  toggleFullScreen(): void;
  isPreviewActive(): boolean;
  isSideBySideActive(): boolean;
  isFullscreenActive(): boolean;
}

interface CodeMirrorEditor {
  getCursor(): { line: number; ch: number };
  getLine(line: number): string;
  replaceRange(replacement: string, from: { line: number; ch: number }, to?: { line: number; ch: number }): void;
  setCursor(pos: { line: number; ch: number }): void;
  getSelection(): string;
  replaceSelection(replacement: string): void;
}

export interface EditorToolbarItem {
  name: string;
  action: (editor: EasyMDEEditor) => void;
  className: string;
  title: string;
}

// üî• ÏûëÍ∞Ä ÏπúÌôîÏ†Å EasyMDE ÏÑ§Ï†ï (FontAwesome ÏïÑÏù¥ÏΩò + ÌïúÍµ≠Ïñ¥ ÏµúÏ†ÅÌôî)
export const getEditorOptions = () => ({
  autofocus: true,
  spellChecker: false,
  placeholder: 'Ïù¥ÏïºÍ∏∞Î•º ÏãúÏûëÌï¥Î≥¥ÏÑ∏Ïöî...',
  status: ['lines', 'words', 'cursor'],
  toolbar: false, // üî• Í∏∞Î≥∏ Ìà¥Î∞î Ïà®Í∏∞Í∏∞ (Ïª§Ïä§ÌÖÄ Lucide Ìà¥Î∞î ÏÇ¨Ïö©)
  shortcuts: {
    "toggleBold": "Cmd-B",        // üî• macOS Ïä§ÌÉÄÏùº Îã®Ï∂ïÌÇ§
    "toggleItalic": "Cmd-I", 
    "drawLink": "Cmd-K",
    "toggleHeading1": "Cmd-Alt-1", // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº Ìó§Îî© Îã®Ï∂ïÌÇ§
    "toggleHeading2": "Cmd-Alt-2",
    "toggleHeading3": "Cmd-Alt-3",
    "cleanBlock": "Cmd-E",
    "drawImage": "Cmd-Alt-I",
    "toggleUnorderedList": "Cmd-Shift-8", // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº Î¶¨Ïä§Ìä∏
    "toggleOrderedList": "Cmd-Shift-7",   // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº Î≤àÌò∏ Î¶¨Ïä§Ìä∏
    "toggleBlockquote": "Cmd-Shift-9",    // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº Ïù∏Ïö©Íµ¨
    "toggleCodeBlock": "Cmd-Alt-C",
    "togglePreview": "Cmd-P",
    "toggleSideBySide": "F9",
    "toggleFullScreen": "F11",
    "toggleDarkMode": "Cmd-D" // üî• Îã§ÌÅ¨Î™®Îìú ÌÜ†Í∏Ä Îã®Ï∂ïÌÇ§ Ï∂îÍ∞Ä
  },
  // üî• ÌïúÍ∏Ä ÏûÖÎ†• ÏµúÏ†ÅÌôî ÏÑ§Ï†ï (2024-2025 ÏµúÏã† IME ÏßÄÏõê)
  inputStyle: "contenteditable" as const, // üî• IME ÏßÄÏõê Ìñ•ÏÉÅÏùÑ ÏúÑÌï¥ contenteditable ÏÇ¨Ïö©
  nativeSpellcheck: true, // üî• IMEÏôÄ Ìï®Íªò ÎÑ§Ïù¥Ìã∞Î∏å Ïä§Ìé†Ï≤¥ÌÅ¨ ÌôúÏÑ±Ìôî
  previewRender: (plainText: string) => {
    return `<div class="prose dark:prose-invert max-w-none">${plainText}</div>`;
  },
  renderingConfig: {
    singleLineBreaks: false,
    codeSyntaxHighlighting: true,
  },
  autoDownloadFontAwesome: false, // üî• FontAwesome ÎπÑÌôúÏÑ±Ìôî (LucideÎ°ú ÍµêÏ≤¥ ÏòàÏ†ï)
  tabSize: 2,
  lineWrapping: true,
  styleSelectedText: false, // üî• ÏÑ±Îä• Ìñ•ÏÉÅÏùÑ ÏúÑÌï¥ ÎπÑÌôúÏÑ±Ìôî
  parsingConfig: {
    allowAtxHeaderWithoutSpace: true,
  },
  lineNumbers: false,
  mode: {
    name: "markdown",
    highlightFormatting: false, // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùºÏùÑ ÏúÑÌï¥ ÎßàÌÅ¨ÏóÖ Ïà®Í∏∞Í∏∞
  },
  // üî• ÌïúÍ∏Ä ÏûÖÎ†• ÏµúÏ†ÅÌôî ÏÑ§Ï†ï (IME Î∞©Ìï¥ ÏöîÏÜå Ï†úÍ±∞)
  configureMouse: () => ({ addNew: false }),
  indentWithTabs: false,
  smartIndent: false, // üî• Ïä§ÎßàÌä∏ Ïù∏Îç¥Ìä∏ ÎπÑÌôúÏÑ±Ìôî (ÌïúÍ∏Ä ÏûÖÎ†• Î∞©Ìï¥ Î∞©ÏßÄ)
  electricChars: false, // üî• ÏûêÎèô Î¨∏Ïûê ÍµêÏ†ï ÎπÑÌôúÏÑ±Ìôî (ÌïúÍ∏Ä Ï°∞Ìï© Î∞©Ìï¥ Î∞©ÏßÄ)
  rtlMoveVisually: true,
  // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº Ïª§Ïä§ÌÖÄ ÌÇ§ ÎßµÌïë (Ïª§ÏÑú ÏúÑÏπò Î≥¥Ï°¥ Í∞ïÌôî)
  extraKeys: {
    // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº ÍµµÍ≤å (Cmd+B) - Í∏∞Í∞ÄÏ∞®Îìú ÏàòÏ†ï: Ìè¨Ïª§Ïä§ Ï°∞Ïûë Ï†úÍ±∞
    "Cmd-B": function(cm: any) {
      const cursor = cm.getCursor();
      const selection = cm.getSelection();
      
      if (selection) {
        cm.replaceSelection(`**${selection}**`);
      } else {
        cm.replaceSelection('****');
        cm.setCursor({ line: cursor.line, ch: cursor.ch + 2 });
      }
    },
    
    // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº Í∏∞Ïö∏ÏûÑ (Cmd+I) - Í∏∞Í∞ÄÏ∞®Îìú ÏàòÏ†ï: Ìè¨Ïª§Ïä§ Ï°∞Ïûë Ï†úÍ±∞
    "Cmd-I": function(cm: any) {
      const cursor = cm.getCursor();
      const selection = cm.getSelection();
      
      if (selection) {
        cm.replaceSelection(`*${selection}*`);
      } else {
        cm.replaceSelection('**');
        cm.setCursor({ line: cursor.line, ch: cursor.ch + 1 });
      }
    },
    
    // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº ÎßÅÌÅ¨ (Cmd+K) - Í∏∞Í∞ÄÏ∞®Îìú ÏàòÏ†ï: Ìè¨Ïª§Ïä§ Ï°∞Ïûë Ï†úÍ±∞
    "Cmd-K": function(cm: any) {
      const cursor = cm.getCursor();
      const selection = cm.getSelection();
      
      if (selection) {
        cm.replaceSelection(`[${selection}]()`);
        const newCursor = cm.getCursor();
        cm.setCursor({ line: newCursor.line, ch: newCursor.ch - 1 });
      } else {
        cm.replaceSelection('[ÎßÅÌÅ¨ ÌÖçÏä§Ìä∏]()');
        cm.setSelection(
          { line: cursor.line, ch: cursor.ch + 1 },
          { line: cursor.line, ch: cursor.ch + 6 }
        );
      }
    },
    
    // üî• Í∏∞Í∞ÄÏ∞®Îìú ÎßàÌÅ¨Îã§Ïö¥ Î≥ÄÌôò: Space ÌÇ§Î°ú ÎßàÌÅ¨ÏóÖ ÏûêÎèô Î≥ÄÌôò
    "Space": function(cm: any) {
      const cursor = cm.getCursor();
      const line = cm.getLine(cursor.line);
      const lineStart = line.substring(0, cursor.ch);
      
      // 1. Ìó§Îî© Ìå®ÌÑ¥ Í∞êÏßÄ (# 1-6Í∞ú) - Ï†ïÌôïÌïú ÎßàÌÅ¨Îã§Ïö¥ Î≥ÄÌôò
      const headingMatch = lineStart.match(/^(#{1,6})$/);
      if (headingMatch) {
        // Îã®ÏàúÌûà Ïä§ÌéòÏù¥Ïä§Îßå Ï∂îÍ∞Ä (### -> ### )
        cm.replaceSelection(' ');
        return;
      }
      
      // 2. Î¶¨Ïä§Ìä∏ Ìå®ÌÑ¥ Í∞êÏßÄ (- ÎòêÎäî *)
      const listMatch = lineStart.match(/^([-*])$/);
      if (listMatch) {
        cm.replaceSelection(' ');
        return;
      }
      
      // 3. Î≤àÌò∏ Î¶¨Ïä§Ìä∏ Ìå®ÌÑ¥ Í∞êÏßÄ (1. 2. Îì±)
      const numberedListMatch = lineStart.match(/^(\d+\.)$/);
      if (numberedListMatch) {
        cm.replaceSelection(' ');
        return;
      }
      
      // 4. Ïù∏Ïö©Íµ¨ Ìå®ÌÑ¥ Í∞êÏßÄ (>)
      const quoteMatch = lineStart.match(/^(>)$/);
      if (quoteMatch) {
        cm.replaceSelection(' ');
        return;
      }
      
      // Í∏∞Î≥∏ Ïä§ÌéòÏù¥Ïä§ ÏûÖÎ†•
      cm.replaceSelection(' ');
    }
  }
});

