'use client';

import { useState, useCallback } from 'react';
import { Logger } from '../../../../shared/logger';
import { useTheme } from '../../../providers/ThemeProvider';

interface UseUIStateReturn {
  // UI ÏÉÅÌÉú
  showLeftSidebar: boolean;
  showRightSidebar: boolean;
  showHeader: boolean;
  isDarkMode: boolean;
  isFocusMode: boolean;
  
  // Ïï°ÏÖò
  toggleLeftSidebar: () => void;
  toggleRightSidebar: () => void;
  toggleDarkMode: () => void;
  toggleFocusMode: () => void;
  setShowHeader: (show: boolean) => void;
}

export function useUIState(): UseUIStateReturn {
  // üî• ÌÖåÎßà Í¥ÄÎ¶¨Î•º ThemeProviderÎ°ú ÏúÑÏûÑ
  const { resolvedTheme, toggleTheme } = useTheme();
  
  // üî• UI ÏÉÅÌÉú (ÌÖåÎßà Ï†úÏô∏)
  const [showLeftSidebar, setShowLeftSidebar] = useState<boolean>(true);
  const [showRightSidebar, setShowRightSidebar] = useState<boolean>(false);
  const [showHeader, setShowHeader] = useState<boolean>(true);
  const [isFocusMode, setIsFocusMode] = useState<boolean>(false);

  // üî• Ìï∏Îì§Îü¨ Ìï®ÏàòÎì§
  const toggleLeftSidebar = useCallback((): void => {
    setShowLeftSidebar(prev => !prev);
    Logger.debug('UI_STATE', 'Left sidebar toggled');
  }, []);
  
  const toggleRightSidebar = useCallback((): void => {
    setShowRightSidebar(prev => !prev);
    Logger.debug('UI_STATE', 'Right sidebar toggled');
  }, []);
  
  const toggleDarkMode = useCallback((): void => {
    toggleTheme(); // ThemeProviderÏùò ÌÜ†Í∏Ä ÏÇ¨Ïö©
    Logger.debug('UI_STATE', 'Dark mode toggled via ThemeProvider');
  }, [toggleTheme]);
  
  const toggleFocusMode = useCallback((): void => {
    setIsFocusMode(prev => {
      const newValue = !prev;
      if (newValue) {
        setShowLeftSidebar(false);
        setShowRightSidebar(false);
      }
      Logger.info('UI_STATE', `Focus mode ${newValue ? 'enabled' : 'disabled'}`);
      return newValue;
    });
  }, []);

  return {
    showLeftSidebar,
    showRightSidebar,
    showHeader,
    isDarkMode: resolvedTheme === 'dark', // ThemeProviderÏóêÏÑú Í∞ÄÏ†∏Ïò¥
    isFocusMode,
    toggleLeftSidebar,
    toggleRightSidebar,
    toggleDarkMode,
    toggleFocusMode,
    setShowHeader
  };
}