export interface WriterStats {
  wordCount: number;
  charCount: number;
  paragraphCount: number;
  readingTime: number;
  wordGoal: number;
  progress: number;
  sessionTime: number;
  wpm: number;
  headingCount?: number;
  listItemCount?: number;
}

// üî• ÏûëÍ∞Ä ÌÜµÍ≥Ñ Í≥ÑÏÇ∞
export const calculateWriterStats = (
  content: string,
  wordGoal: number,
  sessionStartTime: number
): WriterStats => {
  if (!content) {
    return {
      wordCount: 0,
      charCount: 0,
      paragraphCount: 0,
      readingTime: 0,
      wordGoal,
      progress: 0,
      sessionTime: 0,
      wpm: 0,
      headingCount: 0,
      listItemCount: 0
    };
  }
  
  const lines = content.split('\n');
  const wordCount = content.split(/\s+/).filter(word => word.length > 0).length;
  const charCount = content.length;
  const paragraphCount = content.split(/\n\s*\n/).filter(p => p.trim().length > 0).length;
  const readingTime = Math.ceil(wordCount / 200); // Î∂Ñ Îã®ÏúÑ (200 WPM Í∏∞Ï§Ä)
  const progress = Math.min(100, Math.round((wordCount / wordGoal) * 100));
  
  // ÏÑ∏ÏÖò ÏãúÍ∞Ñ Î∞è WPM Í≥ÑÏÇ∞
  const sessionMinutes = Math.max(1, (Date.now() - sessionStartTime) / 1000 / 60);
  const wpm = Math.round(wordCount / sessionMinutes);
  const sessionTime = Math.floor(sessionMinutes);
  
  // ÎßàÌÅ¨Îã§Ïö¥ ÏöîÏÜå Ïπ¥Ïö¥Ìä∏
  const headingCount = lines.filter(line => line.match(/^#{1,6}\s/)).length;
  const listItemCount = lines.filter(line => line.match(/^[\s]*[-*+]\s/)).length;
  
  return {
    wordCount,
    charCount,
    paragraphCount,
    readingTime,
    wordGoal,
    progress,
    sessionTime,
    wpm,
    headingCount,
    listItemCount
  };
};

// üî• ÏãúÍ∞Ñ Ìè¨Îß∑ÌåÖ Ìó¨Ìçº
export const formatTime = (minutes: number): string => {
  if (minutes < 60) return `${minutes}Î∂Ñ`;
  const hours = Math.floor(minutes / 60);
  const mins = minutes % 60;
  return `${hours}ÏãúÍ∞Ñ ${mins ? `${mins}Î∂Ñ` : ''}`;
};

// üî• ÎßàÏßÄÎßâ Ï†ÄÏû• ÏãúÍ∞Ñ Ìè¨Îß∑ÌåÖ
export const formatLastSaved = (lastSaved: Date | null): string => {
  if (!lastSaved) return 'Ï†ÄÏû•ÎêòÏßÄ ÏïäÏùå';
  
  const now = new Date();
  const diffMs = now.getTime() - lastSaved.getTime();
  const diffMins = Math.floor(diffMs / (1000 * 60));
  
  if (diffMins < 1) return 'Î∞©Í∏à Ï†Ñ';
  if (diffMins < 60) return `${diffMins}Î∂Ñ Ï†Ñ`;
  
  const diffHours = Math.floor(diffMins / 60);
  if (diffHours < 24) return `${diffHours}ÏãúÍ∞Ñ Ï†Ñ`;
  
  const diffDays = Math.floor(diffHours / 24);
  return `${diffDays}Ïùº Ï†Ñ`;
};

// üî• Í∏ÄÏì∞Í∏∞ Î™©Ìëú Ï∂îÏ≤ú
export const getRecommendedWordGoal = (averageWordsPerDay: number): number[] => {
  const base = Math.max(500, averageWordsPerDay);
  return [
    Math.round(base * 0.8),
    base,
    Math.round(base * 1.2),
    Math.round(base * 1.5),
    Math.round(base * 2.0)
  ];
};
    