'use client';

import { useCallback, useRef, useEffect } from 'react';
import { Logger } from '../../../../shared/logger';

interface UseAutoSaveOptions {
  projectId: string;
  delay?: number; // Í∏∞Î≥∏Í∞í: 3.5Ï¥à (ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº)
  onSave: () => Promise<void>;
  onSaveSuccess?: () => void;
  onSaveError?: (error: Error) => void;
}

/**
 * üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº ÏûêÎèô Ï†ÄÏû• Hook - ÌÉÄÏù¥Ìïë Ï§ëÎã® ÌõÑ Ï†ÄÏû•
 * 
 * ÌäπÏßï:
 * - ÏÇ¨Ïö©ÏûêÍ∞Ä ÌÉÄÏù¥ÌïëÏùÑ Î©àÏ∂ò ÌõÑÏóêÎßå Ï†ÄÏû• (ÎÖ∏ÏÖò Î∞©Ïãù)
 * - ÌÉÄÏù¥Ìïë Ï§ëÏóêÎäî Ï†àÎåÄ Ï†ÄÏû•ÌïòÏßÄ ÏïäÏùå
 * - 3.5Ï¥à ÎîúÎ†àÏù¥Î°ú Ïó¨Ïú†Î°úÏö¥ ÏÇ¨Ïö©Ïûê Í≤ΩÌóò Ï†úÍ≥µ
 * - ÏóêÎü¨ Ï≤òÎ¶¨ Î∞è Ïû¨ÏãúÎèÑ Î°úÏßÅ
 * - Ï†ÄÏû• ÏÉÅÌÉú Ï∂îÏ†Å
 */
export function useAutoSave({
  projectId,
  delay = 5000, // üî• 5Ï¥àÎ°ú Ï¶ùÍ∞Ä (Í∏Ä ÏÇ≠Ï†ú Î∞©ÏßÄ - Í∏∞Í∞ÄÏ∞®Îìú ÏàòÏ†ï)
  onSave,
  onSaveSuccess,
  onSaveError
}: UseAutoSaveOptions) {
  const saveTimerRef = useRef<NodeJS.Timeout | null>(null);
  const isLoadingRef = useRef(false);
  const retryCountRef = useRef(0);
  const lastTypingTimeRef = useRef<number>(0); // üî• ÎßàÏßÄÎßâ ÌÉÄÏù¥Ìïë ÏãúÍ∞Ñ Ï∂îÏ†Å
  const maxRetries = 3;

  // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº Debounced Ï†ÄÏû• (ÌÉÄÏù¥Ìïë Ï§ëÎã® ÌõÑÏóêÎßå)
  const debouncedSave = useCallback(() => {
    // üî• ÌÉÄÏù¥Ìïë ÏãúÍ∞Ñ ÏóÖÎç∞Ïù¥Ìä∏
    lastTypingTimeRef.current = Date.now();
    
    // Í∏∞Ï°¥ ÌÉÄÏù¥Î®∏ Ï∑®ÏÜå
    if (saveTimerRef.current) {
      clearTimeout(saveTimerRef.current);
    }

    // ÏÉà ÌÉÄÏù¥Î®∏ ÏÑ§Ï†ï - ÏÇ¨Ïö©ÏûêÍ∞Ä ÌÉÄÏù¥ÌïëÏùÑ Î©àÏ∂ò ÌõÑÏóêÎßå Ïã§Ìñâ
    saveTimerRef.current = setTimeout(async () => {
      const timeSinceLastTyping = Date.now() - lastTypingTimeRef.current;
      
      // üî• ÌïµÏã¨: ÎßàÏßÄÎßâ ÌÉÄÏù¥Ìïë ÌõÑ Ï∂©Î∂ÑÌïú ÏãúÍ∞ÑÏù¥ ÏßÄÎÇ¨ÎäîÏßÄ ÌôïÏù∏
      if (timeSinceLastTyping < delay * 0.9) { // ÎîúÎ†àÏù¥Ïùò 90% Ïù¥ÏÉÅ ÎåÄÍ∏∞
        Logger.debug('AUTO_SAVE', 'Still typing, postponing save', { 
          projectId, 
          timeSinceLastTyping,
          requiredDelay: delay 
        });
        // Îã§Ïãú Ïä§ÏºÄÏ§ÑÎßÅ
        debouncedSave();
        return;
      }
      
      if (isLoadingRef.current) {
        Logger.debug('AUTO_SAVE', 'Save already in progress, skipping', { projectId });
        return;
      }

      try {
        isLoadingRef.current = true;
        const startTime = Date.now();
        
        Logger.debug('AUTO_SAVE', 'üíæ Starting auto-save (typing stopped)', { 
          projectId,
          timeSinceLastTyping 
        });
        
        await onSave();
        retryCountRef.current = 0; // ÏÑ±Í≥µ Ïãú Ïû¨ÏãúÎèÑ Ïπ¥Ïö¥ÌÑ∞ Î¶¨ÏÖã
        
        const duration = Date.now() - startTime;
        Logger.info('AUTO_SAVE', '‚úÖ Auto-save completed', { 
          projectId, 
          duration: `${duration}ms`,
          timeSinceLastTyping
        });
        
        onSaveSuccess?.();
      } catch (error) {
        const errorObj = error instanceof Error ? error : new Error(String(error));
        Logger.error('AUTO_SAVE', '‚ùå Auto-save failed', errorObj);
        
        // Ïû¨ÏãúÎèÑ Î°úÏßÅ
        if (retryCountRef.current < maxRetries) {
          retryCountRef.current++;
          Logger.warn('AUTO_SAVE', `Retrying save (${retryCountRef.current}/${maxRetries})`, { projectId });
          
          // Ïû¨ÏãúÎèÑ ÎîúÎ†àÏù¥ (2Ï¥à, 4Ï¥à, 8Ï¥àÎ°ú Ï†êÏßÑÏ†Å Ï¶ùÍ∞Ä)
          setTimeout(() => {
            debouncedSave();
          }, Math.pow(2, retryCountRef.current) * 1000);
        } else {
          Logger.error('AUTO_SAVE', 'Max retries exceeded', { projectId });
          onSaveError?.(errorObj);
        }
      } finally {
        isLoadingRef.current = false;
        saveTimerRef.current = null;
      }
    }, delay);
  }, [projectId, delay, onSave, onSaveSuccess, onSaveError]);

  // üî• Ï¶âÏãú Ï†ÄÏû• (Ctrl+SÏö©)
  const forceSave = useCallback(async () => {
    // Í∏∞Ï°¥ debounced Ï†ÄÏû• Ï∑®ÏÜå
    if (saveTimerRef.current) {
      clearTimeout(saveTimerRef.current);
      saveTimerRef.current = null;
    }

    if (isLoadingRef.current) {
      Logger.debug('AUTO_SAVE', 'Save already in progress, cannot force save', { projectId });
      return;
    }

    try {
      isLoadingRef.current = true;
      const startTime = Date.now();
      
      Logger.info('AUTO_SAVE', 'Starting force save', { projectId });
      
      await onSave();
      retryCountRef.current = 0;
      
      const duration = Date.now() - startTime;
      Logger.info('AUTO_SAVE', '‚úÖ Force save completed', { 
        projectId, 
        duration: `${duration}ms` 
      });
      
      onSaveSuccess?.();
    } catch (error) {
      const errorObj = error instanceof Error ? error : new Error(String(error));
      Logger.error('AUTO_SAVE', '‚ùå Force save failed', errorObj);
      onSaveError?.(errorObj);
    } finally {
      isLoadingRef.current = false;
    }
  }, [projectId, onSave, onSaveSuccess, onSaveError]);

  // üî• Ïª¥Ìè¨ÎÑåÌä∏ Ïñ∏ÎßàÏö¥Ìä∏ Ïãú Ï†ïÎ¶¨
  useEffect(() => {
    return () => {
      if (saveTimerRef.current) {
        clearTimeout(saveTimerRef.current);
      }
    };
  }, []);

  return {
    debouncedSave,
    forceSave,
    isLoading: isLoadingRef.current,
  };
}
