// üî• Î™ÖÎ†πÏñ¥ Î©îÎâ¥ Ïª¥Ìè¨ÎÑåÌä∏
interface CommandMenuProps {
  items: SlashCommand[];
  command: (item: SlashCommand) => void;
}

const CommandMenu = forwardRef<CommandMenuRef, CommandMenuProps>(({ items, command }, ref) => {
  const [selectedIndex, setSelectedIndex] = useState(0);

  useImperativeHandle(ref, () => ({
    onKeyDown: ({ event }: { event: KeyboardEvent }) => {
      if (event.key === 'ArrowUp') {
        setSelectedIndex((selectedIndex + items.length - 1) % items.length);
        return true;
      }

      if (event.key === 'ArrowDown') {
        setSelectedIndex((selectedIndex + 1) % items.length);
        return true;
      }

      if (event.key === 'Enter') {
        selectItem(selectedIndex);
        return true;
      }

      return false;
    },
  }));

  useEffect(() => setSelectedIndex(0), [items]);

  const selectItem = (index: number): void => {
    const item = items[index];
    if (item) {
      command(item);
    }
  };

  return (
    <div className="z-50 w-72 p-1 bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg shadow-lg max-h-80 overflow-auto">
      {items.length ? (
        items.map((item, index) => (
          <button
            key={index}
            className={`flex items-center gap-3 w-full px-3 py-2 text-left text-sm rounded-md transition-colors ${
              index === selectedIndex
                ? 'bg-blue-50 dark:bg-blue-900/20 text-blue-900 dark:text-blue-100'
                : 'text-gray-900 dark:text-gray-100 hover:bg-gray-50 dark:hover:bg-gray-700'
            }`}
            onClick={() => selectItem(index)}
          >
            <div className="w-4 h-4 flex items-center justify-center text-gray-500 dark:text-gray-400">
              <item.icon size={16} />
            </div>
            <div className="flex-1">
              <div className="font-medium">{item.title}</div>
              <div className="text-xs text-gray-500 dark:text-gray-400">{item.description}</div>
            </div>
          </button>
        ))
      ) : (
        <div className="px-3 py-2 text-sm text-gray-500 dark:text-gray-400">Í≤ÄÏÉâ Í≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§</div>
      )}
    </div>
  );
});

CommandMenu.displayName = 'CommandMenu';

// üî• Ïä¨ÎûòÏãú Î™ÖÎ†πÏñ¥ ÌôïÏû•
export const SlashCommand = Extension.create({
  name: 'slashCommand',

  addOptions() {
    return {
      suggestion: {
        char: '/',
        command: ({ editor, range, props }: any) => {
          props.command({ editor, range });
        },
      },
    };
  },

  addProseMirrorPlugins() {
    return [
      Suggestion({
        editor: this.editor,
        ...this.options.suggestion,
      }),
    ];
  },
});

// üî• Ïä¨ÎûòÏãú Î™ÖÎ†πÏñ¥ Ï†úÏïà ÏÑ§Ï†ï
export const slashSuggestion = {
  items: ({ query }: { query: string }) => {
    return SLASH_COMMANDS.filter(item =>
      item.title.toLowerCase().includes(query.toLowerCase()) ||
      item.description.toLowerCase().includes(query.toLowerCase())
    );
  },

  render: () => {
    let component: ReactRenderer;
    let popup: any;

    return {
      onStart: (props: any) => {
        component = new ReactRenderer(CommandMenu, {
          props,
          editor: props.editor,
        });

        if (!props.clientRect) {
          return;
        }

        popup = tippy('body', {
          getReferenceClientRect: props.clientRect,
          appendTo: () => document.body,
          content: component.element,
          showOnCreate: true,
          interactive: true,
          trigger: 'manual',
          placement: 'bottom-start',
        });
      },

      onUpdate(props: any) {
        component.updateProps(props);

        if (!props.clientRect) {
          return;
        }

        popup[0].setProps({
          getReferenceClientRect: props.clientRect,
        });
      },

      onKeyDown(props: any) {
        if (props.event.key === 'Escape') {
          popup[0].hide();
          return true;
        }

        return (component.ref as CommandMenuRef)?.onKeyDown(props);
      },

      onExit() {
        popup[0].destroy();
        component.destroy();
      },
    };
  },
};
