      console.log('üì® AI ÏöîÏ≤≠ ÏãúÏûë:', content.substring(0, 30) + '...');
      
      // Loop OpenAI ÏÑúÎπÑÏä§ ÏßÅÏ†ë Ìò∏Ï∂ú
      const response = await fetch('https://loop-openai.onrender.com/api/chat', {
        method: 'POST',
        headers: { 
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({ 
          message: content
        })
      });
      
